@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@400;500;600;700&display=swap);
* {
  font-family: 'Roboto', sans-serif;
}
body {
  margin: 0;
  height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#root {
  height: 100%;
  overflow: hidden;
}
::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}
::-webkit-scrollbar-thumb {
  background: #a5a5a5;
  border-radius: 15px;
}

::-webkit-scrollbar-thumb:hover {
  background: #5f5f5f;
}
@-moz-document url-prefix() {
  .Friends {
    scrollbar-color: #a5a5a5 transparent;
    scrollbar-width: thin;
  }
}

:root {
  --main-color-Light: #03a9f1;
  --main-color-Dark: #292f3f;
}

.App {
  height: 100%;
  padding-top: 10px;
  box-sizing: border-box;
  background-color: var(--background-home);
}

.t1 {
  font-family: 'Roboto', sans-serif;
  font-size: 22px;
  font-weight: 600;
}
.t2 {
  font-family: 'Roboto', sans-serif;
  font-size: 15px;
  font-weight: 600;
}
.t3 {
  font-family: 'Roboto', sans-serif;
  font-size: 28px;
  font-weight: 600;
}
.t4 {
  font-family: 'Roboto', sans-serif;
  font-size: 17px;
}
.t2-min {
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
}
.sing {
  width: 100%;
  display: flex;
  flex-flow: column;
}
.form-group {
  width: 48%;
}
.form-group > p {
  font-family: 'Roboto', sans-serif;
  font-size: 15px;
  font-weight: 700;
  margin-bottom: 2px;
  margin-left: 2px;
}
.inline-group {
  display: flex;
  justify-content: space-between;
}
@media only screen and (max-width: 885px) {
  .t1 {
    font-size: 26px;
    margin-top: 10px;
  }
}

.header {
  height: 10%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-left: auto;
  margin-right: auto;
  width: 90%;
}
.logo {
  display: flex;
}
.logo img {
  width: 60px;
  height: 55px;
}
.logo p {
  font-family: 'Roboto', sans-serif;
  font-size: 25px;
  font-weight: 600;
  margin-top: 0;
  transform: translateY(15px);
}
.menu {
  display: flex;
  align-items: center;
}
.menu button {
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  font-weight: 700;
  border: none;
  background-color: var(--main-color-Light);
  border-radius: 7px;
  margin-left: 18px;
  cursor: pointer;
  z-index: 2;
}

@media only screen and (max-width: 885px) {
  .logo img {
    width: 40px;
    height: 49px;
  }
  .header {
    margin-left: 25px;
    margin-right: 25px;
  }
}

.Toggle {
  display: flex;
  flex-flow: row;
  justify-content: flex-start;
  align-items: center;
  width: 50px;
  height: 24px;
  background-color: #e5e5e5;
  border-radius: 15px;
  cursor: pointer;
}

.ToggleSwitcher {
  width: 18px;
  height: 18px;
  border-radius: 50%;
}

.body {
  width: 100%;
  height: 90%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.body-titre {
  width: 400px;
  transition: height 1000ms;
  animation: fade-in-left 0.6s;
}
.body-img img {
  transform: translateY(-30px);
  width: 850px;
}
@keyframes fade-in-left {
  0% {
    transform: translateX(-50px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
@media only screen and (max-width: 1185px) {
  .body-img img {
    width: 450px;
  }
}
@media only screen and (max-width: 885px) {
  .body-img img {
    display: none;
  }
  .body {
    justify-content: center;
    align-items: baseline;
    overflow: auto;
  }
  .body-titre {
    margin-left: 20px;
    margin-right: 20px;
  }
}

.Input {
  height: 40px;
  border: none;
  width: 100%;
  border-radius: 8px;
  transition: all 0.5s;
  padding-left: 12px;
  font-family: 'Roboto', sans-serif;
  font-size: 15px;
  font-weight: 600;
  margin-top: 5px;
  box-sizing: border-box;
  background-color: var(--background-Input);
  color: var(--background-Input-color);
}
.input-active {
  border: 1.2px solid #03a9f1;
  outline: none;
  box-shadow: 0px 0px 2px 4px rgba(3, 169, 241, 0.33);
  background-color: var(--background-Input-active);
}

.Input:hover:not([disabled]) {
  border: none;
  outline: none;
  box-shadow: 0px 0px 2px 4px rgba(3, 169, 241, 0.33);
  background-color: var(--background-Input-active);
}

.line {
  width: 100%;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.line hr {
  width: 42%;
  margin: 0px;
  border: none;
  height: 1.1px;
  background-color: #eef2fe;
}
.line > p {
  margin-bottom: 0px;
  font-family: 'Roboto', sans-serif;
  font-size: 15px;
  font-weight: 700;
  margin-top: 0px;
}
.form-sing {
  width: 100%;
}
.form-sing > p {
  display: none;
  text-align: center;
  font-family: 'Roboto', sans-serif;
  font-size: 13px;
  font-weight: 700;
  margin-top: 30px;
}
.form-sing > p span {
  color: var(--main-color-Light);
  cursor: pointer;
}
@media only screen and (max-width: 885px) {
  .form-sing {
    width: auto;
    font-size: 16px;
  }
  .form-sing button {
    width: 100%;
  }
  .form-sing > p {
    display: block;
  }
}

.titre {
  width: 100%;
}
.titre p {
  font-family: 'Roboto', sans-serif;
  font-size: 40px;
  font-weight: 300;
}

@media only screen and (max-width: 885px) {
  .titre p {
    font-size: 40px;
    margin-left: 15px;
    margin-right: 15px;
    text-align: center;
  }
  .titre {
    display: flex;
    align-items: center;
    flex-flow: column;
  }
  .titre Button {
    width: 100px;
  }
}

.Step {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  padding-left: 25px;
  padding-right: 45px;
}
.Step > div {
  height: 75%;
}
.Info-step {
  width: 550px;
  display: flex;
  flex-flow: column;
  justify-content: space-evenly;
}
.Step-count {
  width: 65px;
  transform: translateY(-30px);
}
.image-step {
  width: 550px;
  height: 550px;
}
.Step-count {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
}
.Circle {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  color: white;
  transition: all 0.8s;
}
.Circle-active::before {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  transition: all 0.6s;
  border-radius: 50%;
  border: 1px solid var(--main-color-Light);
  animation: scale-up-center 0.8s;
}
.LineStep {
  width: 3px;
  height: 26px;
  background-color: var(--main-color-Light);
}

@keyframes scale-up-center {
  0% {
    transform: scale(0.5);
  }
  100% {
    transform: scale(1.2);
  }
}
.Info-step > p:nth-of-type(1) {
  margin-left: 20px;
}
.Info-step > div:nth-of-type(1) {
  width: 100%;
  height: 380px;
  margin-left: 55px;
  animation: fade-in-left 0.6s;
  justify-content: center;
  display: flex;
  flex-direction: column;
}
.Info-step > div:nth-of-type(2) {
  width: 100%;
  height: 15%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  animation: fade-in-bottom 0.6s;
}
.abs {
  animation: fade-in-left 0.8s;
}
@keyframes fade-in-left {
  0% {
    transform: translateX(-70px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes fade-in-bottom {
  0% {
    transform: translateY(50px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

.Switch-btn,
.choose-btn {
  width: 70%;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.choosePicture {
  width: 85%;
  height: 250px;
  border: 3px dashed var(--main-color-Light);
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  border-radius: 10px;
  margin-bottom: 15px;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
}
.choosePicture:hover {
  background-color: #dbedff;
  border-color: var(--main-color-Light);
  transition: all 0.5s;
}
.choosePicture > img:nth-of-type(1) {
  width: 65px;
}
.choosePicture > p:nth-of-type(1) {
  color: #777;
  font-family: 'Quicksand';
  font-weight: 600;
}
.choosePicture > input:nth-of-type(1) {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  cursor: pointer;
}
.pictures {
  width: 500px;
  height: 256px;
  align-items: center;
  justify-content: center;
  padding-left: 15px;
  display: flex;
  flex-wrap: wrap;
  overflow-y: auto;
}
.pictures::-webkit-scrollbar {
  display: none;
}
.picture {
  width: 120px;
  height: 120px;
  margin-right: 8px;
  border-radius: 4px;
  cursor: pointer;
  z-index: 1;
  position: relative;
  overflow: hidden;
  margin-bottom: 8px;
}
.picture > img:nth-of-type(1) {
  width: 100%;
  height: 100%;
  border-radius: 4px;
}
.picture::before {
  position: absolute;
  top: 0px;
  left: 0px;
  content: '';
  width: 0px;
  height: 100%;
  transition: all 0.5s;
  border-radius: 4px;
}
.picture:hover::before {
  width: 100%;
  background-color: rgba(57, 62, 70, 0.3);
}
.btnRemove {
  height: 40px;
  top: -42px;
  left: 22px;
}
.btnDefault {
  height: 40px;
  bottom: -42px;
  left: 4px;
}
.picture:hover .btnRemove {
  top: 20px;
}
.picture:hover .btnDefault {
  bottom: 20px;
}

@media only screen and (max-width: 1314px) {
  .image-step {
    display: none;
  }
}
@media only screen and (max-width: 1185px) {
  .Step > div {
    height: 85%;
  }
}
@media only screen and (max-width: 885px) {
  .Info-step {
    width: 80%;
  }
  .Step-count {
    width: 10%;
  }

  .Step {
    padding-left: 0px;
    padding-right: 0px;
  }
  .Info-step > div:nth-of-type(1) {
    padding-left: 8px;
    height: 100%;
  }
  .Step > div {
    height: 100%;
  }
  .Circle {
    width: 35px;
    height: 35px;
  }
  .choosePicture {
    width: 80%;
    height: 80px;
  }
  .choosePicture > p:nth-of-type(1) {
    font-size: 14px;
  }
  .choosePicture > img:nth-of-type(1) {
    display: none;
  }
  .pictures {
    width: 80%;
    height: auto;
    margin-left: auto;
    margin-right: auto;
  }
  .picture {
    width: 100px;
    height: 80px;
  }
  .Info-step > div:nth-of-type(1) {
    height: 100%;
  }
  .Info-step > div:nth-of-type(1) {
    margin-left: 0px;
  }
  .Switch-btn,
  .choose-btn {
    margin-left: auto;
    margin-right: auto;
  }
  .picture:hover .btnRemove {
    top: 5px;
  }
  .picture:hover .btnDefault {
    bottom: 5px;
  }
}

input[type='date'] {
  padding: 9px 16px 9px 16px;
  border-radius: 8px;
  font-family: 'Roboto', sans-serif;
  font-size: 15px;
  font-weight: 600;
  background-color: var(--background-Input);
  border: none;
  box-sizing: border-box;
  position: relative;
  color: var(--background-Input-color);
}
.dateBirthday {
  border: 1px solid var(--main-color-Light);
  width: 200px;
}
input[type='date']::-webkit-calendar-picker-indicator {
  width: 20px;
  height: 20px;
  cursor: pointer;
}

input[type='date']:focus {
  outline: none;
  box-shadow: 0px 0px 2px 4px rgba(3, 169, 241, 0.33);
}

.Select {
  width: 88%;
  min-height: 200px;
  height: 100%;
  max-height: 250px;
  max-width: 400px;
  display: flex;
  flex-flow: column;
  border: 1px solid rgb(185, 185, 185);
  border-radius: 4px;
  margin: 5px 0;
  color: var(--color-QuickActionsMenu) !important;
}
.SelectInput {
  display: flex;
  flex-flow: row;
  justify-content: center;
  height: 50px;
  width: 100%;
  margin-top: 10px;
}
.SelectInput .MuiTextField-root {
  width: 90%;
}
.SelectItems {
  width: 100%;
  height: 100%;
  max-height: 65px;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid rgb(185, 185, 185);
  overflow-y: auto;
  box-sizing: border-box;
}
.SelectItems .MuiChip-root {
  margin: 4px;
}
.SelectList {
  width: 100%;
  display: flex;
  flex-flow: column;
  overflow-y: auto;
  height: 200px;
}
.SelectListItem {
  width: 100%;
  height: 35px;
  display: flex;
  flex-flow: column;
  justify-content: center;
  padding: 20px;
  font-size: 17px;
  cursor: pointer;
  box-sizing: border-box;
}
.SelectListItem:hover {
  background-color: var(--background-Friend-Hover);
}
.Select input::-webkit-input-placeholder {
  color: var(--color-QuickActionsMenu) !important;
}
.Select input::-moz-placeholder {
  color: var(--color-QuickActionsMenu) !important;
}
.Select input:-ms-input-placeholder {
  color: var(--color-QuickActionsMenu) !important;
}
.Select input::placeholder {
  color: var(--color-QuickActionsMenu) !important;
}

.ft_btn {
  height: 38px;
  border: none;
  border-radius: 8px;
  background-color: var(--main-color-Light);
  padding-right: 15px;
  padding-left: 15px;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  font-weight: 700;
  color: white;
  cursor: pointer;
  outline: none;
  transition: all 0.5s;
}
.ft_btn-2 {
  height: 38px;
  width: 100px;
  border-radius: 18px;
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  letter-spacing: 2px;
  background-color: transparent;
  font-weight: 700;
  color: var(--main-color-Light);
  border: 1.5px solid var(--main-color-Light);
  cursor: pointer;
  outline: none;
  transition: all 0.5s;
  position: relative;
  z-index: 1;
  box-sizing: border-box;
}
.ft_btn-3 {
  height: 32px;
  width: 100px;
  border-radius: 18px;
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  letter-spacing: 2px;
  background-color: var(--main-color-Light);
  font-weight: 700;
  color: white;
  border: 1.5px solid var(--main-color-Light);
  cursor: pointer;
  outline: none;
  transition: all 0.5s;
}
.ft_btn-2:hover {
  color: white;
  border: none;
}
.ft_btn-2:after {
  position: absolute;
  content: '';
  width: 0;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
  background-color: var(--main-color-Light);
  border-radius: 18px;
  transition: all 0.3s ease;
}
.ft_btn-2:hover:after {
  width: 100%;
}

.ft_btn:active,
.ft_btn:focus {
  border: 1.2px solid #03a9f1;
  outline: none;
  box-shadow: 0px 0px 2px 4px rgba(3, 169, 241, 0.33);
  border: none;
}
.ft_btn:hover {
  border: none;
  outline: none;
  box-shadow: 0px 0px 2px 4px rgba(3, 169, 241, 0.33);
  background-color: #40a8c4;
}
@keyframes jello-horizontal {
  0% {
    transform: scale(1, 1);
  }
  30% {
    transform: scale(1.25, 0.75);
  }
  40% {
    transform: scale(0.75, 1.25);
  }
  50% {
    transform: scale(1.15, 0.85);
  }
  65% {
    transform: scale(0.95, 1.05);
  }
  75% {
    transform: scale(1.05, 0.95);
  }
  100% {
    transform: scale(1, 1);
  }
}
.AnimationBtn {
  animation: jello-horizontal 1s;
}

.Icons {
  cursor: pointer;
}

.Nav {
  width: 285px;
  min-width: 285px;
  height: 100%;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: space-between;
  background-color: var(--background-Nav);
  overflow: hidden;
  transition: 0.5s linear;
}
.Nav * {
  opacity: 1;
  transition: opacity 0.5s linear;
}
.Nav .Icons {
  fill: var(--color-NavLink-Hover);
  transition: 0s;
}
.NavHide {
  width: 0;
  min-width: 0px;
}
.NavHide * {
  opacity: 0;
}
.Logo {
  display: flex;
  flex-flow: row;
  align-items: center;
  height: 100px;
}
.Logo img {
  width: 65px;
}
.Menu {
  display: flex;
  flex-flow: column;
  align-items: center;
}
.NavLink {
  width: 165px;
  height: 50px;
  margin: 10px;
  display: flex;
  flex-flow: row;
  justify-content: flex-start;
  align-items: center;
  padding: 0 15px;
  font-size: 18px;
  text-decoration: none;
  color: white;
  border-radius: 25px;
}
.NavLink span:last-child {
  margin: 0 30px;
}
.Menu .NavLink:hover:not(.NavLinkActive) {
  background-color: var(--background-NavLink-Hover);
  color: var(--color-NavLink-Hover);
}
.NavLinkActive {
  background-color: var(--background-NavLink-Active);
  color: var(--color-NavLink-Hover);
  font-weight: bold;
}
.Logout {
  margin: 15px;
}
.Logout .NavLink {
  border: 2px solid white;
  border-radius: 6px;
  box-sizing: border-box;
  justify-content: center;
}
.Logout .NavLink:hover {
  background-color: #fd6363;
  border: none;
}

.ImageLoader,
.ImageLoader img {
  width: 100%;
  height: 100%;
}
.ImageLoading {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.ImageLoading::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #2d2d2d;
  animation: AnimateImageLoading 1.5s infinite ease-in-out;
}

@keyframes AnimateImageLoading {
  0% {
    background-color: #2d2d2d;
  }
  50% {
    background-color: #4d4c4c;
  }
  100% {
    background-color: #2d2d2d;
  }
}

.Friends {
  width: 100%;
  min-width: 100%;
  display: flex;
  flex-flow: column;
  align-items: center;
  overflow-y: auto;
  margin: 0 4px;
  transition: 0.5s linear;
}
.Friends .Icons {
  fill: var(--Icon-Fill);
}
.Friend {
  width: 95%;
  height: 80px;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-around;
  align-items: center;
  background: var(--background-Friend);
  border-radius: 15px;
  padding: 5px;
  margin: 5px;
  margin-left: 10px;
  cursor: pointer;
  box-sizing: border-box;
  transition: 0.5s linear;
}
.Friend:hover {
  background-color: var(--background-Friend-Hover);
}
.FriendImage {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  padding: 2px;
}
.FriendImage .FriendImageImage {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  overflow: hidden;
}
.FriendInfo {
  width: 60%;
  display: flex;
  flex-flow: column;
  margin: 10px;
}
.FriendInfo span:first-child {
  font-weight: 600;
  font-size: 13px;
  text-transform: capitalize;
  color: var(--color-FriendInfo-firstChild);
  margin: 2px 0;
}
.FriendInfo span:last-child {
  color: var(--color-FriendInfo-lastChild);
  font-size: 12px;
  font-weight: 600;
  box-sizing: border-box;
  margin: 2px 0;
}
.FriendInfo span:last-child .Icons {
  fill: #44db44;
}
.FriendProfile button,
.FriendMessage button {
  border: none;
  outline: none;
  background-color: transparent;
